import NewsList from "@/components/news-list/news-list.vue"
import uniLoadMore from "@/components/uni-load-more/uni-load-more"
export default {
	data() {
		return {
			newsList:[],
			loadingText: {
				contentdown: '上拉加载更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			loadStatus: 'more',
			hasMore: true,
			page:1
		}
	},
	onLoad() {
		this.getNewsList(1);
	},
	onPullDownRefresh() {
		this.getNewsList(1);
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onReachBottom() {
		this.getNewsList(2)
	},
	methods: {
		getNewsList(action = 1){
			if (this.hasMore == false) {
				return false;
			}
			if (action === 1) {
				this.page = 1;
			}else{
				this.page++;
			}
			this.loadStatus = 'loading';
			this.$uniRequest(
				this.$baseUrl + '/news/message',
				{
					page:this.page,
					listRows:10
				}, 
				(res) =>{
					if (res.data.code == 1 && res.data.data.list) {
						var data = res.data.data.list;
						if(action === 1){
							this.newsList = data;
							this.loadStatus = 'more';
						} else {
							data.forEach((item)=>{
								this.newsList.push(item);
							});
							this.loadStatus = 'more';
						}
						if(data.length < 10){
							this.hasMore = false;
							this.loadStatus = 'noMore';
						}
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg,
							showCancel: false,
							confirmText: '知道啦'
						});
						this.hasMore = false;
						this.loadStatus = 'noMore';
					}
					setTimeout(function() {
						uni.hideLoading();
					}, 500);
				}
			)
		}
	},
	components:{
		NewsList,
		uniLoadMore
	}
}
