	},
	props:{
		newsList:{
			type:Array
		}
	},
	filters:{
		formatDate:function(data){
			var date =  new Date(data * 1000);
			return date.getFullYear() +'-'+ date.getMonth() +'-'+ date.getDate() +' '+date.getHours() +':'+date.getMinutes()
		}
	},
	methods:{
		goDetail(item){
			uni.navigateTo({
				url: '/pages/detail/detail?url=' + item.titleurl
			});
		}
	}
}
